import React, { Component } from "react";
import SkillSectionDivider from './SkillSectionDivider';
import SkillSection from './SkillSection';
import './style.css'

class Skills extends Component {
    render() {
        return (
            <div id="skills">
                <SkillSectionDivider/>
                <SkillSection/>
            </div>
        )
    }
}

export default Skills;