import React from 'react';
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import './style.css'
export default class Example extends React.Component {
  render() {
    return (
      <Form>
        <FormGroup>
          <Label for="contactName">Your Name</Label>
          <Input type="text" name="contactName" id="contactName" placeholder="Your Name" />
        </FormGroup>
        <FormGroup>
          <Label for="contactEmail">Your Email</Label>
          <Input type="email" name="contactEmail" id="contactEmail" placeholder="Email" />
        </FormGroup>
        <FormGroup>
          <Label for="exampleText">Text Area</Label>
          <Input type="textarea" name="text" id="exampleText" placeholder="Your message here." />
        </FormGroup>
        <Button id="submitButton">Submit</Button>
      </Form>
    );
  }
}