import BackgroundImg from './clouds.jpg';


export default class Background {
    constructor(state) {
        console.log(state)
        this.screenHeight = state.screen.height;
        this.screenWidth = state.screen.width;
        this.x = 0;
        this.y = this.screenHeight - this.screenHeight;
        this.speedX = 0;
        this.speedY = 0;
        this.scene = state.scene;
    }

    render(state) {
        
        const context = state.context;
        let image = new Image();
        image.src = BackgroundImg;
        image.onload = () => {
            //drawing selveral background images with different x cordinates to give a single picture an edless effect
            context.drawImage(image, 0, 0, state.canvasWidth, state.canvasHeight);
        };
        context.restore();
    }
}
