import LeprechaunImg from './leprechaun.png';

export default class Leprechaun {
    constructor(props) {
        console.log("props: ", props);
        this.props = props;
        this.width = props.canvasWidth/5;
        this.height = props.canvasWidth/5;
        this.x = props.canvasWidth/3;
        this.y = props.canvasHeight - this.width;
        this.curFrame = 0;
        this.frameCount = 8;
        this.speedX = 0;
        this.speedY = 0;
        this.rockBottom = this.screenHeight - this.height;
    }

    updateFrame(){
        this.curFrame = ++this.curFrame % this.frameCount; 
        // context.clearRect(this.x,this.y,this.width,this.height);	
    }

    render(state) {
        this.updateFrame();
        if (state.keys.left === true) {
            this.x -= 5;
        } else if (state.keys.right === true) {
            this.x += 5;
        } else {
            this.speedX = 0
        }
   

        const context = state.context;

        let image = new Image();
        image.src = LeprechaunImg; 
        image.onload = () => {
            context.drawImage(image, this.x, this.y, this.width, this.height);
        };
        // console.log(image);
        context.restore();
    }
}