import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import htmlLogo from '../../images/skillLogos/html.png'
import cssLogo from '../../images/skillLogos/css.png'
import jsLogo from '../../images/skillLogos/js.png'

import './style.css'

class ProjectsSectionDivider extends Component {
    render() {
        return (
            <div>
                <Container fluid id="">
                    <Row>
                        <Col xs="3" md="2" lg="1">
                            <img className="projectPic" src={htmlLogo} alt="" />
                            <h5>HTML5</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="projectPic" src={cssLogo} alt="" />
                            <h5>CSS3</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="projectPic" src={jsLogo} alt="" />
                            <h5>JavaScript</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="projectPic" src={jsLogo} alt="" />
                            <h5>JavaScript</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                        <p>See more of my work on gitub.com/jaksik</p>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default ProjectsSectionDivider;