import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import htmlLogo from '../../images/skillLogos/html.png'
import cssLogo from '../../images/skillLogos/css.png'
import jsLogo from '../../images/skillLogos/js.png'
import reactLogo from '../../images/skillLogos/react.png'
import sqlLogo from '../../images/skillLogos/sql.png'
import mongoLogo from '../../images/skillLogos/mongo.png'
import jqueryLogo from '../../images/skillLogos/jquery.png'
import nodeLogo from '../../images/skillLogos/node.png'
import expressLogo from '../../images/skillLogos/express.png'
import handlebarsLogo from '../../images/skillLogos/handlebars.png'
import bootstrapLogo from '../../images/skillLogos/bootstrap.png'
import gitLogo from '../../images/skillLogos/git.png'

import './style.css'

class SkillSectionDivider extends Component {
    render() {
        return (
            <div>
                <Container fluid id="skillSectionDivider">
                    <Row>
                        <Col xs="12">
                        <h1>Technical Skills</h1>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={htmlLogo} alt="" />
                            <h5>HTML5</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={cssLogo} alt="" />
                            <h5>CSS3</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={jsLogo} alt="" />
                            <h5>JavaScript</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={reactLogo} alt="" />
                            <h5>React JS</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={sqlLogo} alt="" />
                            <h5>SQL</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={mongoLogo} alt="" />
                            <h5>MongoDB</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={jqueryLogo} alt="" />
                            <h5>jQuery</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={nodeLogo} alt="" />
                            <h5>Node JS</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={expressLogo} alt="" />
                            <h5>Express</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={handlebarsLogo} alt="" />
                            <h5>Handlebars</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={bootstrapLogo} alt="" />
                            <h5>Bootstrap</h5>
                        </Col>
                        <Col xs="3" md="2" lg="1">
                            <img className="skillPic" src={gitLogo} alt="" />
                            <h5>Git</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="12">
                            <p>And a few Technologies that I'm most passionate about....</p>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default SkillSectionDivider;