import React, { Component } from 'react';
import Leprechaun from './Leprechaun';
import GoldCoin from '../../images/canvasDemo/gold-coin.png';
import Background from './CanvasBackground';

class Canvas extends Component {
    constructor() {
        super();
        this.state = {
            screen: {
                width: window.innerWidth,
                height: window.innerHeight,
                ratio: window.devicePixelRatio || 1,
            },
            canvasWidth: window.innerWidth * .8,
            canvasHeight: window.innerHeight * .4,
            
            context: null,
            keys: {
                left: false,
                right: false
            },
        };
        this.canvasWidth= window.innerWidth * .8;
        this.canvasHeight= window.innerHeight * .4;
        this.componentWidth = window.innerWidth/30;
        // this.myScore = new component("30px", "Consolas", "white", 200, 40, "text");
        this.leprechaun = new Leprechaun(this.state);
       this.background = new Background(this.state);
        // this.goldCoinOne = new component(this.componentWidth, this.componentWidth, GoldCoin, Math.floor((Math.random() * screenWidth) + 1), -(this.componentWidth), "image");
        // this.goldCoinTwo = new component(this.componentWidth, this.componentWidth, GoldCoin, Math.floor((Math.random() * screenWidth) + 1), (this.componentWidth * 2), "image");
        
    }

    handleKeys(value, e) {
        console.log("value: ", value)
        console.log("e", e)
        let keys = this.state.keys;
        if (e.keyCode === 37) keys.left = value;
        if (e.keyCode === 39) keys.right = value;
        this.setState({
            keys: keys
        });
    }
    componentDidMount() {
        window.addEventListener('keydown', this.handleKeys.bind(this, true));
        window.addEventListener('keyup', this.handleKeys.bind(this, false));
        const context = this.refs.canvas.getContext('2d');
        this.setState({ context: context });
        this.startGame();
        requestAnimationFrame(() => { this.update() });
    }

    startGame() {
    }

    update() {

        const context = this.state.context;
        
        context.save();
        context.globalAlpha = 0.001;
        // context.scale(this.state.screen.ratio, this.state.screen.ratio);

        context.fillRect(0, 0, 400, 400);
        context.fillStyle="#FF0000"

        this.background.render(this.state);
        this.leprechaun.render(this.state);

        
        context.restore();


        requestAnimationFrame(() => { this.update() });
    }

    render() {
        return (
            <div>
                <canvas ref="canvas"
                    width={this.canvasWidth}
                    height={this.canvasHeight}
                />
            </div>
        )
    }
}

export default Canvas;