import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import connorPic from '../../images/connor-profile.png';
import './style.css'

class About extends Component {
    render() {
        return (
            <Container fluid id="about">
                <Row>
                    <Col xs="12" sm="8" md="4" lg="3" push={{ lg: 3 }}>
                        <img src={connorPic} id="profilePic" alt="" />
                    </Col>
                    <Col xs="12" lg="9" pull={{ lg: 9 }}>
                        <p id="bioParagraph">Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.Hello world, my name is Conor and I like to code.</p>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default About;