import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import ProjectImgs from './ProjectsDivider';

class Projects extends Component {
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col xs="12" lg="4">
                            <h1>Some of my work</h1>
                        </Col>
                    </Row>
                </Container>
                <ProjectImgs/>
            </div>
        )
    }
}

export default Projects;