import React, { Component } from "react";
import { Container, Row, Col } from 'reactstrap';
import Canvas from './canvasDemo';
import ReactDemo from './ReactDemo';
import './style.css'

class SkillSection extends Component {
    render() {
        return (
            <div id="skills">
                <Container fluid id="skillDetails">
                    <Row>
                        <Col xs="12" className="skillParagraph">
                            <h3>JavaScript</h3>
                            <p>THIS APP IS IN DEVELOPMENT AND BEING TESTED. DO NO POST SENSATIVE INFORMATION THIS IS NOT A SECURE LOGIN.</p>
                        </Col>
                        <Canvas/>
                    </Row>
                    <Row>
                        <Col xs="12" className="skillParagraph">
                            <h3>React</h3>
                            <p>THIS APP IS IN DEVELOPMENT AND BEING TESTED. DO NO POST SENSATIVE INFORMATION THIS IS NOT A SECURE LOGIN.</p>
                        </Col>
                        <ReactDemo/>
                    </Row>
                    <Row>
                        <Col xs="12" className="skillParagraph">
                            <h3>Algorithms</h3>
                            <p>THIS APP IS IN DEVELOPMENT AND BEING TESTED. DO NO POST SENSATIVE INFORMATION THIS IS NOT A SECURE LOGIN.</p>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="12" className="skillParagraph">
                            <h3>Neural Networks</h3>
                            <p>THIS APP IS IN DEVELOPMENT AND BEING TESTED. DO NO POST SENSATIVE INFORMATION THIS IS NOT A SECURE LOGIN.</p>
                        </Col>
                    </Row>

                </Container>
            </div>
        )
    }
}

export default SkillSection;